      windowWidth: 0,
      windowHeight: 0
  }),
  computed: {
    style: function() {
      if (this.offsetTop === 0) return 'opacity: 0.5'
      else return ''
    },
    headingClass: function() {
      switch (this.$vuetify.breakpoint.name) {
        case 'xs': return 'text-h4'
        case 'sm': return 'text-h3'
        case 'md': return 'text-h2'
        case 'lg': return 'text-h1'
        case 'xl': return 'text-h1'
        default: return 'text-h3'
      }
    }
  },
  methods: {
    onScroll (e) {
      this.offsetTop = e.target.scrollingElement.scrollTop
    },
    sendWhatsapp () {
      window.open(
          `https://api.whatsapp.com/send?phone=${this.phoneNumber}&text=${this.phoneMessage}`,
          "_blank"
        );
    },
    sendMail () {
      window.open(`mailto:${this.email}?subject=subject&body=${this.emailMessage}`);
    },
    openInstagram () {
      window.open(this.instagram);
    },
    openFacebook () {
      window.open(this.facebook);
    },
    onResize () {
      this.windowWidth = window.innerWidth
      this.windowHeight = window.innerHeight
    }
  },
  mounted () {
    this.onResize()
    window.addEventListener('resize', this.onResize, { passive: true })
  }
};
